export default function List({item,deleted}) {
    return (
        <div>
             <div>
            <ul>
                {item.map(function(name){
                return(
                    <li>
                    <span>
                    <a href = {name.url}>{name.title}</a>
                    </span>
                    <span>{name.author}</span>
                    <span>{name.num_comments}</span>
                    <span>{name.points}</span>
                    <span><button onClick={()=> deleted(name)}>delete</button></span>
                    </li>
                );
           })}
            </ul>
            
        </div>
        </div>
    );
}