
import { useEffect, useState } from 'react'

export default function useSemiPersistenceState(key,initValue) {
 const[persistenceKey,setpersistenceKey] = useState(localStorage.getItem(key)||initValue);
 useEffect(()=>{
     localStorage.setItem(key,persistenceKey)
 },)
 return [persistenceKey,setpersistenceKey]
}